import Swiper from 'swiper'
import 'swiper/dist/css/swiper.min.css';

export default {
  name: "Promotion",
  data: function () {
    return {
      baseUrl: 'http://gateway.zan-qian.com/',
      sendBindWxMsgRequest: 'message-service/1.0.0/sms/sendBindWxMsg',
      verificationCodeRequest: 'message-service/1.0.0/sms/verificationCode',
      acceptShareUserActivityRewardRequest: 'promotion-service/1.0.0/share_activity/acceptShareUserActivityReward',
      findUserActivityRewardTraceRequest: 'promotion-service/1.0.0/share_activity/findUserActivityRewardTrace',
      getAdvertiseRequest: 'advertising-service/1.0.0',

      swiperInstance: {},
      loginId: null,
      verifyCode: '',
      smsTemplate: 3,
      advertiseList: [],
      activityId: '',
      prizeStatus: '',
      receiveRewardFlag: false,
      rewardTraceListData: [],
      acceptPrizeFlag:false,
      prizeData: {
        code: '',
        data: {
          description: '',
          loginId: '',
          rewardStr: '',
          rewardType: '',
          rewardValue: ''
        },
        message: ''
      },
      prizeTypeDictionary: [{
        name: '趣豆',
        code: 'coin'
      }, {
        name: '积分',
        code: 'point'
      }, {
        name: '百视通',
        code: 'bes_tv'
      }],
      activityStatusDictionary: [{
        code: 10000,
        text: '恭喜你,抽中了！',
        type: 'withPicture'
      }, {
        code: 10001,
        text: '已参加活动',
        type: 'withoutPicture'
      }, {
        code: 10002,
        text: '今日领取次数已达上限',
        type: 'withoutPicture'
      }, {
        code: 10003,
        text: '已领取奖励',
        type: 'withoutPicture'
      }, {
        code: 10004,
        text: '哎呀，晚了一步',
        type: 'withoutPicture'
      }, {
        code: 10005,
        text: '领取奖励失败',
        type: 'withoutPicture'
      }, {
        code: 10006,
        text: '活动不在有效期内',
        type: 'withoutPicture'
      }, {
        code: 10007,
        text: '活动不存在',
        type: 'withoutPicture'
      }]
    }
  },
  computed: {
    userActivityId() {
      return this.$route.query.id || '2';
    },
    identityCode() {
      return this.$route.query.id || '2';

    }
  },
  created() {

  },
  watch: {
    verifyCode(value) {
      if (value.length >= 5) {
        this.receiveRewardFlag = true;
      }
      console.log(this.receiveRewardFlag)
    }
  },
  mounted() {
    this.$autoHeight({
      target: '.common_main_container',
    });
    this.$remResizing({
      fontSize: 20,
    });
    this.getAdvertise();
    this.getRewardTraceList();
  },
  methods: {
    login: function () {
      console.log(this.$refs['loginFormData'])
      console.log(this.loginFormData)
      this.$refs['loginFormData'].validate(function (valid) {
        console.log(valid)
        if (valid) {

        }
      })
    },
    sendSmsCode: function () {
      this.$http.get(this.$baseUrl + this.sendBindWxMsgRequest + '/' + this.loginId).then(response => {
        console.log(response)
        if (response.alreadySent === true) {
          alert('短信已发出，请查收')
        }
        if (response.alreadySent === false) {
          alert(response.message)
        }
      }).catch(errpr => {
        alert(error)
      })
    },
    checkVerifyCode(value) {
      console.log(this.verifyCode.length)

    },
    getRewardTraceList() {
      this.$http.get(this.$baseUrl + this.findUserActivityRewardTraceRequest + `/${this.userActivityId}`, {
        headers: {
          // Authorization: 'Bearer b95a6b75-4767-431a-ba4f-b7db8abcbe5e'
        }
      }).then(response => {
        console.log(response)
        this.rewardTraceListData = response.data;
        this.rewardTraceListData.forEach((item, index) => {
          this.$set(this.rewardTraceListData, index, Object.assign(this.rewardTraceListData[index], {
            availible: true
          }))
          // this.rewardTraceListData[index].availible = true;
        })
      }).catch(error => {
        console.log(error)
      })
    },
    getAdvertise() {
      let deviceData = this.$getDevice();
      let deviceType;
      let location = 'qu_welfare';
      if (deviceData.ios) {
        deviceType = 'ios'
      } else if (deviceData.android) {
        deviceType = 'android'
      } else {
        Object.keys(deviceData).forEach((item1, index1) => {
          if (deviceData[item1] === true) {
            deviceType = item1
          }
        });
      }
      if (deviceType !== 'ios' && deviceType !== 'android') {
        deviceType = 'ios';
      }

      this.$http.get(this.$baseUrl + this.getAdvertiseRequest + `/${deviceType}/${location}`, {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
          // Authorization: 'Bearer b95a6b75-4767-431a-ba4f-b7db8abcbe5e'
        }
      }).then(response => {
        console.log(response)

        this.advertiseList = response;
        this.$nextTick(() => {
          this.swiperInstance = new Swiper('.swiper-container', {
            autoplay: true,
            loop: true
          })
        })
      }).catch(error => {
        console.log(error)
      })
    },
    acceptPrize() {
      if (this.receiveRewardFlag) {
        this.$http.post(this.$baseUrl + this.acceptShareUserActivityRewardRequest + `/${this.userActivityId}/${this.loginId}`, {}, {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        }).then(response => {
          console.log(response)
          this.prizeData = response;
          this.acceptPrizeFlag=true;
        }).catch(error => {
          console.log(error)
        })
      } else {
        alert('验证码格式不正确')
      }

    },
    imageError(index) {
      console.log(this.rewardTraceListData)
      console.log(index)

      this.$set(this.rewardTraceListData, index, Object.assign(this.rewardTraceListData[index], {
        availible: false
      }))
      // this.rewardTraceListData[index].availible = false;
    }
  }
}



